<template>
<div id="upload">
  <div class="upload-container">
    <h2>Upload</h2>
    <form class="input-group" id="uploadForm" method="POST" action="/api" enctype="multipart/form-data" @submit.prevent="handleFormSubmission">
      <label class="file">
        <input class="file-input" id="fileInput" type="file" multiple name="video" @change="updateFiles">
        <span class="file-control">{{uploadFiles.length ? uploadFiles.length + ' Files' : 'Choose Files...'}}</span>
      </label>
      <input class="btn btn-primary" id="submit" type="submit" value="Submit" :disabled="submitButtonDisabled"></form>
  </div>
  <div class="upload-info">
    <h4>
      <span class="speed">{{speed}}</span>
      <span class="elapsed">{{elapsed}}</span>
      <span class="uploaded">{{uploaded}}</span>
    </h4>
    <div class="progress">
      <div class="progress-bar bg-cyan" :style="{ width: `${progress}%`}">{{progress}}%</div>
    </div>
  </div>
  <div class="delete-container">
    <h2>Delete</h2>
    <div class="input-group">
      <select class="videoSelect" multiple v-model="deleteSelect">
        <option disabled="">Select Media File...</option>
        <option v-for="file in files" :value="file.id" :label="file.name"></option>
      </select>
      <input class="btn btn-danger" id="deleteSubmitButton" type="submit" value="Delete" @click="handleDeleteSubmission">
    </div>
  </div>
</div>
</template>

<script>
const bytesToMb = (bytes) => (bytes * 1e-6).toFixed(2);
export default {
  name: 'Upload',
  data() {
    return {
      deleteSelect: [],
      files: [],
      submitButtonDisabled: false,
      uploadFiles: [],
      progress: 0,
      speed: '',
      elapsed: '',
      uploaded: ''
    }
  },
  methods: {
    updateFiles(e) {
      this.uploadFiles = e.target.files;
    },
    fetchFiles() {
      fetch('/api')
        .then(res => res.json())
        .then(res => {
          this.files = res;
        });
    },
    handleFormSubmission(e) {
      if (!this.uploadFiles.length) return;
      let startTime = Date.now();
      this.submitButtonDisabled = true;
      const formData = new FormData();
      const xhr = new XMLHttpRequest();
      console.log(this.uploadFiles);
      [...this.uploadFiles].forEach(file => formData.append('video', file, file.name));
      xhr.open('POST', '/api', true);
      xhr.upload.addEventListener('progress', (e) => {
        if (e.lengthComputable) {
          const {
            loaded,
            total
          } = e;

          this.progress = Math.round((loaded * 100) / total);

          const time = (Date.now() - startTime) / 1000;
          const uploadSpeed = (bytesToMb(loaded) / time).toFixed(2);

          this.speed = `${uploadSpeed} MB/s`;
          this.elapsed = `${time.toFixed(2)} s`;

          const mbLoaded = bytesToMb(loaded).slice(0, -3);
          const mbTotal = bytesToMb(total).slice(0, -3);
          this.uploaded = `${mbLoaded} MB of ${mbTotal} MB`;
        }
      });
      ['error', 'abort'].forEach(ev => xhr.upload.addEventListener(ev, this.handleFormSubmissionError));

      xhr.addEventListener('load', () => {
        this.submitButtonDisabled = false;
        this.uploadFiles = [];
        this.progress = 0;
        this.speed = '';
        this.elapsed = '';
        this.uploaded = '';
        this.fetchFiles();
      });
      xhr.send(formData);
    },
    handleFormSubmissionError(err) {
      console.log(err);
    },
    handleDeleteSubmission() {
      return Promise.all(this.deleteSelect.map(id => {
          return fetch(`/api/video/${id}`, {
            method: 'DELETE'
          });
        }))
        .then(() => {
          this.fetchFiles();
        });
    }
  },
  created() {
    this.fetchFiles();
  }
}
</script>

<style scoped>
.progress {
  width: 100%;
  display: flex;
  height: 1rem;
  overflow: hidden;
  font-size: 0.75rem;
  background-color: #e9ecef;
  border-radius: 0.25rem;
}

.progress-bar {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  background-image: linear-gradient(45deg, rgba(255, 255, 255, 0.15) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 0.15) 50%, rgba(255, 255, 255, 0.15) 75%, transparent 75%, transparent);
  background-size: 1rem 1rem;
  animation: progress-bar-stripes 1s linear infinite;
}

.upload-container,
.delete-container,
.upload-info {
  background-color: #f5f5f5;
  padding: 2rem;
}

.input-group {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.input-group>*:not(:last-child) {
  flex: 1;
}

input[type='submit'] {
  appearance: button;
  margin: 0 1rem;
}

@keyframes progress-bar-stripes {
  from {
    background-position: 1rem 0;
  }
  to {
    background-position: 0 0;
  }
}

select {
  display: inline-block;
  max-width: 100%;
  height: calc(2.25rem + 2px);
  padding: 0.375rem 1.75rem 0.375rem 0.75rem;
  line-height: 1.25;
  color: #495057;
  vertical-align: middle;
  background: #fff url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 4 5'%3E%3Cpath fill='%23333' d='M2 0L0 2h4zm0 5L0 3h4z'/%3E%3C/svg%3E") no-repeat right 0.75rem center;
  background-clip: padding-box;
  background-size: 8px 10px;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 0.25rem;
  appearance: none;
  height: 10rem;
  font-size: 1rem;
}

select:focus {
  border-color: #80bdff;
  outline: none;
}

select:disabled {
  color: #868e96;
  background-color: #e9ecef;
}

.file {
  position: relative;
  display: inline-block;
  max-width: 100%;
  height: calc(2.25rem + 2px);
  margin-bottom: 0;
  outline: none;
  cursor: pointer;
}

.file-input {
  min-width: 14rem;
  max-width: 100%;
  height: calc(2.25rem + 2px);
  margin: 0;
  opacity: 0;
}

.file-input:focus~.file-control {
  box-shadow: 0 0 0 0.075rem #fff, 0 0 0 0.2rem #007bff;
}

.file-control {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 5;
  height: calc(2.25rem + 2px);
  padding: 0.5rem 0.75rem;
  overflow: hidden;
  line-height: 1.25;
  color: #495057;
  pointer-events: none;
  user-select: none;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 0.25rem;
}

.file-control:lang(en):empty::after {
  content: "Choose files...";
}

.file-control::before {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 6;
  display: block;
  height: calc(2.25rem + 2px);
  padding: 0.5rem 0.75rem;
  line-height: 1.25;
  color: #495057;
  background-color: #e9ecef;
  border-left: 1px solid rgba(0, 0, 0, 0.15);
}

.file-control:lang(en)::before {
  content: "Browse";
}

.upload-info h4 {
  display: flex;
  flex-direction: row;
}

.upload-info h4 span {
  flex: 1;
  text-align: center;
}
</style>



