<template>
<div id="show">
  <h1 class="display-6 mt-3">Rick and Morty</h1>
  <ul id="seasons" class="nav nav-pills nav-fill my-4 bg-grey">
    <li class="nav-item" v-for="season in Object.keys(seasons)">
      <a class="nav-link" href="#" @click.prevent="activeSeason = season" :class="{ active: activeSeason === season}">Season {{season}}</a>
    </li>
  </ul>
  <div class="card-deck">
    <FileCard v-for="(file, index) in seasons[activeSeason]" :file="file" :key="index"></FileCard>
  </div>
</div>
</template>

<script>
import FileCard from './FileCard.vue';

export default {
  name: 'Show',
  props: ['title', 'files'],
  data() {
    return {
      activeSeason: '1'
    }
  },
  components: {
    FileCard
  },
  computed: {
    seasons() {
      const obj = this.files.reduce((acc, curr) => {
        if (acc.hasOwnProperty(curr.info.season)) {
          acc[curr.info.season].push(curr)
        } else {
          acc[curr.info.season] = [curr];
        }
        return acc;
      }, {});
      Object.keys(obj).forEach(s => {
        obj[s] = obj[s].sort((a, b) => {
          return a.info.number - b.info.number;
        });
      });
      return obj;
    }
  }
}
</script>
<style scoped>
#show {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#seasons a {
  color: #fff;
}
.card-deck {
  margin: 0;
}
#seasons li:hover {
  background-color: rgba(0, 0, 0, 0.1);
}
</style>



