<template>
<div class="card">
  <div class="card-img-top" :style="{ backgroundImage: `url(${file.info.image})` }" alt="episode image"></div>
  <div class="card-body">
    <div class="main-card-body-wrapper">
      <p class="card-text">S{{pad(file.info.season)}}E{{pad(file.info.number)}}</p>
      <h4 class="card-title">{{file.info.name}}</h4>
      <p class="card-text">{{file.info.summary}}</p>
    </div>
    <div class="button-group">
      <button type="button" class="btn btn-outline-secondary" @click="() => vlc(file)">VLC</button>
      <button type="button" class="btn btn-outline-secondary" @click="() => watch(file)">Watch</button>
      <button type="button" class="btn btn-outline-secondary" @click="() => copy(file)">Copy</button>
      <!-- <button type="button" class="btn btn-secondary btn-sm" @click="() => transcode(file)">Transcode</button> -->
    </div>
  </div>
  <div class="card-footer">
    <small class="text-muted">{{(file.size * 1e-6).toFixed(0)}} MB</small>
  </div>
</div>
</template>

<script>
export default {
  name: 'FileCard',
  props: ['file'],
  methods: {
    pad(num) {
      return num < 10 ? '0' + num : num;
    },
    watch(file) {
      const addr = `/api/video/${file.id}`;
      const a = document.createElement('a');
      a.href = addr;
      a.target = '_blank';
      a.click();
    },
    copy(file) {
      const addr = `${file.protocol}://${file.addr}/api/video/${file.id}`;
      const textArea = document.createElement('textarea');
      textArea.value = addr;
      document.body.appendChild(textArea);
      textArea.select();
      try {
        const successful = document.execCommand('copy');
        const msg = successful ? 'successful' : 'unsuccessful';
        console.log('Copying of link was ' + msg);
      } catch (err) {
        console.log('Copying of link failed.');
      }
      document.body.removeChild(textArea);
    },
    vlc: async(file) => {
      try {
        const addr = `/api/link/${file.id}`;
        const data = await fetch(addr).then(res => res.blob());
        const a = document.createElement('a');
        const url = URL.createObjectURL(data);
        a.href = url;
        a.download = 'playlist.vlc';
        a.click();
        URL.revokeObjectURL(url);
      } catch (err) {
        console.log(err);
      }
    },
    transcode(file) {
      const addr = `http://192.168.1.12:3000/api/video/transcode/${file.id}`;
      const a = document.createElement('a');
      a.href = addr;
      a.target = '_blank';
      a.click();
    }
  }
}
</script>
<style scoped>
.card {
  flex: 0 1 calc((100% - 6rem) / 3);
  margin-bottom: 2rem;
}

.card-body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.button-group {
  margin-top: 1rem;
}

.card-img-top {
  width: 100%;
  height: 225px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
</style>



