<template>
<div class="hello container-fluid">
  <Show v-for="(show, index) in Object.keys(shows)" :title="show" :files="shows[show]" :key="index"></Show>
</div>
</template>

<script>
import Show from './Show.vue';

export default {
  name: 'hello',
  components: {
    Show
  },
  data() {
    return {
      files: []
    }
  },
  computed: {
    shows() {
      return this.files.reduce((acc, curr) => {
        if (acc.hasOwnProperty(curr.info.title)) {
          acc[curr.info.title].push(curr)
        } else {
          acc[curr.info.title] = [curr];
        }
        return acc;
      }, {});
    }
  },
  created() {
    fetch('/api')
      .then(res => res.json())
      .then(res => res.map(file => {
        file.info.summary = file.info.summary.slice(3, file.info.summary.length - 4);
        return file;
      }))
      .then(res => this.files = res);
  }
}
</script>
<style scoped>
</style>



